h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

.main {
  display: flex;
  justify-content: center;
}

.container {
  margin-top: 10vh;
  display: inline-grid;
  border: 1px solid #999;
  padding: 3vw;
  box-shadow: 0 0 5px 2px #ccc;
}

.container > h6 {
  font-style: italic;
  font-weight: 500;
}

.container > * {
  width: 20vw;
}

.container > p {
  font-size: 1vw;
  margin: 0 auto;
}

.container > input {
  margin: 0.5vh;
  padding: 1vh;
  border: 1px solid #cccccc;
}

.container > input:focus {
  border: 1px solid #999999;
}

.container > label {
  margin: 1vh 0 0 1vh;
}

.container > button {
  margin: 0.5vh;
  padding: 1.5vh;
  outline: 0;
  border: none;
  border-radius: 10px;
  background-color: #9932cc;
  color: white;
  width: 50%;
  cursor: pointer;
}

.container > button:hover {
  background-color: #a459c9;
}
